import types from "../type"
const onInputActive = () => {
    const inputs = document.querySelectorAll("input");
    const label = document.querySelectorAll("label");

    inputs.forEach(i => {
        if (i === document.activeElement || i.value !== "") {
            const name = i.name;

            label.forEach(l => {
                const labelName = l.htmlFor;

                if (name === labelName) {
                    l.classList.add("label-onclick-size")
                }
            })
        } else {
            const name = i.name;

            label.forEach(l => {
                const labelName = l.htmlFor;

                if (name === labelName) {
                    l.classList.remove("label-onclick-size")
                }
                const body = document.querySelector("body");
                body.addEventListener("click", () => {

                    if (name === labelName && i.value === "") {
                        l.classList.remove("label-onclick-size")
                    }
                })
            })
        }
    })
}
