const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.DELETE:
            const newState = state.users.filter(u => u.id !== action.users)
            return {
                users: newState
            }
            case types.ADD:
                return {
                    ...state, users: [...state.users, action.users]
                }
                case "IS_SUBMITTING":
                    return {
                        ...state, userLogin: action.userLogin
                    }
                    default:
                        return state
    }
}

export default userReducer