import React from "react";
import { NavLink } from "react-router-dom"
import actions from "../redux/actions/add-user"
import { connect } from "react-redux"
import { motion } from "framer-motion"

class Result extends React.Component {
    render() {
        const validLogin = this.props.userData.filter(u => u.email === this.props.loginEmail).filter(u => u.password === this.props.loginPassword)

        if (validLogin.length !== 0 && this.props.users.userLogin === true && this.props.isSubmitting === true) {
            this.props.add(validLogin.map(u => { return (u.name) }))
            return (
                <motion.p
                    animate={{ opacity: 1 }}
                    initial={{ opacity: 0 }}
                    className="result">
                    You are login {validLogin.map(u => { return (u.name) })}
                </motion.p>
            )
        } else if (validLogin.length === 0 && this.props.isSubmitting === true && this.props.users.userLogin === false) {
            return (
                <motion.p
                    animate={{ opacity: 1 }}
                    initial={{ opacity: 0 }}
                    className="result"
                >
                    Sorry, but we don't have you in our data base!
                <NavLink to="/" style={{ color: "red" }}>Let's register then!</NavLink>
                </motion.p>
            )
        } else return null
    }
}

const mapStateToProps = (state) => ({
    users: state.users,
    userLogin: state.userLogin
})

const mapDispatchToProps = (dispatch) => ({
    add: (name) => dispatch(actions.loginUser(name)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Result);