import React, { Component } from 'react';
import { NavLink } from "react-router-dom"

class Nav extends Component {
    render() {
        let fristPage = { left: "0px" }

        if (window.innerWidth < 1000) {
            if (this.props.location.pathname === "/dataBase") fristPage = { bottom: "-60px" }
            else if (this.props.location.pathname === "/singIn") fristPage = { bottom: "-30px" }
            else fristPage = { bottom: "0px" }
        } else {
            if (this.props.location.pathname === "/dataBase") fristPage = { left: "230px" }
            else if (this.props.location.pathname === "/singIn") fristPage = { left: "110px" }
            else fristPage = { left: "0px" }
        }
        return (
            <nav>
                <div className="logo"></div>
                <ul className="nav-login">
                    <NavLink to="/" activeClassName="active " style={{ position: "relative" }} exact>
                        <li className="login">Sing Up</li>
                        <span className="active-route" style={fristPage} ></span>
                    </NavLink>
                    <NavLink to="/singIn" activeClassName="active" exact>
                        <li className="login">Sing In</li>
                    </NavLink>
                    <NavLink to="/dataBase" activeClassName="active " exact>
                        <li className="login">Data Base</li>
                    </NavLink>
                </ul>
            </nav >
        )
    }
}

export default Nav;