import React from "react"
import { connect } from "react-redux"
import { motion } from "framer-motion"
import actions from "../redux/actions/add-user"

const avatarVariants = {
    in: {
        opacity: 1,
        rotateY: 0
    },
    out: {
        opacity: 0,
        rotateY: 90
    }
}

const nameVariants = {
    in: {
        opacity: 1,
        rotateY: 0,
        y: 0
    },
    out: {
        opacity: 0,
        rotateY: 90,
        y: 40
    }
}

const sectionVariants = {
    in: {
        opacity: 1,
        y: 0
    },
    out: {
        opacity: 0,
        y: -100
    }
}

class UserProfile extends React.Component {

    render() {
        if (this.props.users.userLogin) {

            return (
                <motion.section className="user-profile"
                    variants={sectionVariants}
                    animate={"in"}
                    initial={"out"}
                >
                    <motion.div className="user-avatar"
                        variants={avatarVariants}
                        animate={"in"}
                        initial={"out"}
                        transition={{ duration: 1 }}
                    >
                        <i className="fas fa-user-tie"></i>
                    </motion.div>
                    <motion.div
                        className="user-name"
                        variants={nameVariants}
                        animate={"in"}
                        initial={"out"}
                        transition={{ duration: 1 }}
                    >{this.props.users.userLogin ? this.props.loginUser.name : null}
                    </motion.div>
                    <button className="log-out" onClick={() => this.props.add(false)}>Log Out</button>
                </motion.section>

            )
        } else return null
    }
}

const mapStateToProps = (state) => ({
    users: state.users,
    loginUser: state.loginUser,
    userLogin: state.userLogin
})

const mapDispatchToProps = (dispatch) => ({
    add: (isSubmitting) => dispatch(actions.userLogin(isSubmitting))
})


export default connect(mapStateToProps, mapDispatchToProps)(UserProfile)