import type from "../type"

const addUser = users => ({
    type: type.ADD,
    users
})

const labelUp = labelUp => ({
    type: "LABEL_UP",
    labelUp
})

const deleteUser = users => ({
    type: type.DELETE,
    users
})

const loginUser = loginUser => ({
    type: "LOGIN_USER",
    loginUser
})

const userLogin = userLogin => ({
    type: "IS_SUBMITTING",
    userLogin
})

export default {
    addUser,
    deleteUser,
    labelUp,
    loginUser,
    userLogin
}