import { combineReducers } from "redux";
import userReducer from "./redux/indexRedux"
import planetsReducer from "./redux/indexRedux"
import functionsReducer from "./redux/indexRedux"
import loginUserReducer from "./redux/indexRedux"

const conectReducer = combineReducers({
    users: userReducer.userReducer,
    planets: planetsReducer.planetsReducer,
    functions: functionsReducer.functionsReducer,
    loginUser: loginUserReducer.loginUserReducer
})

export default conectReducer