import React from 'react'
import { connect } from "react-redux"
import actions from "../redux/actions/add-user"
import { motion } from "framer-motion"
import FlipMove from "react-flip-move"

const variants = {
    in: {
        left: 0,
        opacity: 1,
        rotate: 0
    },
    out: {
        left: -400,
        opacity: 0,
        rotate: 20,
    }
}
const DataBase = (props) => {

    const deleteUser = (id) => {
        props.add(id)
    }
    return (
        <motion.section
            className="table-section"
            variants={variants}
            animate={"in"}
            initial={"out"}
            exit="out"
            transition={{
                duration: 0.5
            }}
        >
            <table>
                <thead>
                    <tr>
                        <th>
                            Username
                            </th>
                        <th>
                            Second Name
                             </th>
                        <th>
                            E-Mail
                             </th>
                        <th>
                            Password
                             </th>
                        <th>
                            Repeat Password
                             </th>
                        <th>
                            Code Phone
                              </th>
                        <th>
                            Phone Number
                             </th>
                        <th>
                            Country
                            </th>
                        <th>
                            Gender
                             </th>
                        <th>
                            Delete User
                            </th>
                    </tr>
                </thead>
                <FlipMove typeName="tbody">
                    {props.users.users.map((users) => {
                        return (
                            <tr key={users.id}>
                                <td >{users.name}</td>
                                <td >{users.secondName}</td>
                                <td >{users.email}</td>
                                <td >{users.password}</td>
                                <td >{users.repeatPassword}</td>
                                <td >{users.codePhone}</td>
                                <td >{users.phoneNumber}</td>
                                <td >{users.planet}</td>
                                <td >{users.gender}</td>
                                <td>
                                    <button onClick={() => {
                                        deleteUser(users.id)
                                    }}
                                        className="delete-user-btn"
                                    >
                                        Delete</button>
                                </td>
                            </tr>
                        )
                    })}
                </FlipMove>
            </table >
        </motion.section>
    );

}
const mapStateToProps = (state) => ({
    users: state.users
})

const mapDispatchToProps = dispatch => ({
    add: (id) => dispatch(actions.deleteUser(id))
})
export default connect(mapStateToProps, mapDispatchToProps)(DataBase)