import React from 'react';
import '../App.css';
import { connect } from "react-redux"
import actions from "../redux/actions/add-user"
import { Formik } from "formik"
import Error from "./error"
import * as yup from "yup"
import { motion } from "framer-motion"

const variants = {
    in: {
        opacity: 1,
        x: 0,
    }, out: {
        opacity: 0,
        x: 1000,
    }
}

class SingUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            planets: []
        }
    }

    validationSchema = yup.object().shape({

        user: yup.string().min("3", "Your username sholud have more min 3 letters").required("You have to fill this input"),
        second_name: yup.string().min("2", "Your second name sholud have more min 2 letters").required("You have to fill this input"),
        email: yup.string().email("Incorrect e-mail").required("You have to fill this input"),
        password: yup.string().min("4", "Password must have 4 or more characters").required("You have to fill this input"),
        repeat_password: yup.string().min("4", "Password must have 4 or more characters").required("You have to fill this input"),
        phone_number: yup.string().min("9", "Number should have 9 numbers").max("9", "Number should have 9 numbers").required("You have to fill this input"),
        gender: yup.string().required("Choose your sex")
    })

    render() {

        const planets = "planets";

        async function getData() {
            try {
                let res = await fetch(`https://swapi.co/api/${planets}`);
                if (res.status === 200) {
                    let data = await res.json();

                    return data
                }
            } catch (err) {
                console.log(err)
            }
        }

        getData().then(res => {
            this.setState({
                planets: res.results
            })
        })

        return (
            <motion.article
                animate="in"
                initial={"out"}
                exit="out" t
                ransition={{ duration: 0.4, delay: 0.2 }}
                variants={variants}>
                <header>
                    <h1 className="headers">
                        Order Form
                    </h1>
                </header>
                <section>
                    <Formik
                        initialValues={{
                            user: "",
                            second_name: "",
                            email: "",
                            password: "",
                            repeat_password: "",
                            phone_number: "",
                            code_phone: "+48",
                            planet: "Alderaan",
                            gender: ""
                        }}
                        validationSchema={this.validationSchema}
                        onSubmit={(values, { setSubmitting }) => {
                            const highestId = this.props.users.users.length + 1;
                            if (values.password !== values.repeat_password) {
                                setSubmitting(false)
                            }
                            else {
                                setSubmitting(true);
                                this.props.add(
                                    values.user,
                                    values.second_name,
                                    values.email,
                                    highestId,
                                    values.password,
                                    values.repeat_password,
                                    values.code_phone,
                                    values.phone_number,
                                    values.planet,
                                    values.gender)
                            }
                        }
                        }
                    >
                        {({
                            values,
                            errors,
                            touched,
                            handleChange,
                            handleBlur,
                            handleSubmit,
                            isSubmitting
                        }) => (
                                <form action="POST" method="" onSubmit={handleSubmit}>
                                    <motion.div className="username-section"
                                        animate={{ y: 0, scale: 1 }}
                                        initial={{ y: -200, scale: 1.3 }}
                                        exit={{ y: -200 }}
                                        transition={{ delay: 0.3 }}>
                                        <input
                                            type="text"
                                            name="user"
                                            id="username-input"
                                            className={touched.user && errors.user ? "has-error inputs-style" : "inputs-style"}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={this.props.functions.functions}
                                            required
                                        />
                                        <label
                                            htmlFor="user"
                                            className="labels-style"
                                            style={errors.user ? { color: "red" } : null} >
                                            Type your username:
                                        </label>
                                        <span className="inputs-icon"><i className="fas fa-user-tie"></i></span>
                                        <Error message={errors.user} touched={touched.user} />
                                    </motion.div>
                                    <motion.div className="second-name-section"
                                        animate={{ scale: 1, opacity: 1, display: "block" }}
                                        initial={{ opacity: 0, scale: 0.3, display: "none" }}
                                        exit={{ scale: 0, opacity: 0 }}
                                        transition={{ delay: 0.5 }}>
                                        <input
                                            type="text"
                                            name="second_name"
                                            id="second-name-input"
                                            className={touched.second_name && errors.second_name ? "has-error inputs-style" : "inputs-style"}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={this.props.functions.functions}
                                            required
                                        />
                                        <label
                                            htmlFor="second_name"
                                            className="labels-style"
                                            style={errors.second_name ? { color: "red" } : null} >
                                            Type your second name:
                                        </label>
                                        <span className="inputs-icon"><i className="fas fa-user-edit"></i></span>
                                        <Error message={errors.second_name} touched={touched.second_name} />
                                    </motion.div>
                                    <motion.div className="email-section"
                                        animate={{ y: 0, scale: 1 }}
                                        initial={{ y: -200, scale: 1.3 }}
                                        exit={{ y: -200 }}
                                        transition={{ delay: 0.3 }}>
                                        <input
                                            type="email"
                                            name="email"
                                            id="email-input"
                                            className={touched.email && errors.email ? "has-error inputs-style" : "inputs-style"}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={this.props.functions.functions}
                                            required
                                        />
                                        <label
                                            htmlFor="email"
                                            className="labels-style"
                                            style={errors.email ? { color: "red" } : null}
                                        >
                                            Type your E-mail:
                                        </label>
                                        <span className="inputs-icon"><i className="fas fa-envelope"></i></span>
                                        <Error message={errors.email} touched={touched.email} />
                                    </motion.div>
                                    <motion.div className="password-section"
                                        animate={{ scale: 1, opacity: 1, display: "block" }}
                                        initial={{ opacity: 0, scale: 0.3, display: "none" }}
                                        exit={{ scale: 0, opacity: 0 }}
                                        transition={{ delay: 0.7 }}>
                                        <input
                                            type="password"
                                            name="password"
                                            id="password-input"
                                            className={touched.password && errors.password ? "has-error inputs-style" : "inputs-style"}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={this.props.functions.functions}
                                            required
                                        />
                                        <label
                                            htmlFor="password"
                                            className="labels-style"
                                            style={errors.password ? { color: "red" } : null} >
                                            Type your password
                                        </label>
                                        <span className="inputs-icon"><i className="fas fa-key"></i></span>
                                        <Error message={errors.password} touched={touched.password} password={values.password} repeatPassword={values.repeat_password} />
                                    </motion.div>
                                    <motion.div className="password-section"
                                        animate={{ y: 0, scale: 1 }}
                                        initial={{ y: -200, scale: 1.3 }}
                                        exit={{ y: -200 }}
                                        transition={{ delay: 0.5 }}>
                                        <input
                                            type="password"
                                            name="repeat_password"
                                            id="repeat-password-input"
                                            className={touched.repeat_password && errors.repeat_password ? "has-error inputs-style" : "inputs-style"}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={this.props.functions.functions}
                                            required
                                        />
                                        <label
                                            htmlFor="repeat_password"
                                            className="labels-style"
                                            style={errors.repeat_password ? { color: "red" } : null} >
                                            Type your password
                                        </label>
                                        <span className="inputs-icon"><i className="fas fa-lock"></i></span>
                                        <Error message={errors.repeat_password} touched={touched.repeat_password} password={values.password} repeatPassword={values.repeat_password} />
                                    </motion.div>
                                    <section className="phone-section">
                                        <motion.div className="phone-code"
                                            animate={{ scale: 1, opacity: 1, display: "block" }}
                                            initial={{ opacity: 0, scale: 0.3, display: "none" }}
                                            exit={{ scale: 0, opacity: 0 }}
                                            transition={{ delay: 0.9 }}>
                                            <select
                                                name="code_phone"
                                                id="code-phone"
                                                className="inputs-style"
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                            >
                                                <option value="+48">+48</option>
                                                <option value="+237">+237</option>
                                                <option value="+45">+45</option>
                                            </select>
                                            <i className="fas fa-arrow-down code-phone-arrow"></i>
                                        </motion.div>
                                        <motion.div className="phone"
                                            animate={{ scale: 1, opacity: 1, display: "block" }}
                                            initial={{ opacity: 0, scale: 0.3, display: "none" }}
                                            exit={{ scale: 0, opacity: 0 }}
                                            transition={{ delay: 1 }}>
                                            <label htmlFor="code-phone"></label>
                                            <input
                                                type="number"
                                                name="phone_number"
                                                className={touched.phone_number && errors.phone_number ? "has-error inputs-style" : "inputs-style"}
                                                id="phone-number"
                                                onChange={handleChange}
                                                onClick={this.props.functions.functions}
                                                onBlur={handleBlur}
                                            />
                                            <label htmlFor="phone_number"
                                                className="labels-style"
                                                style={errors.phone_number ? { color: "red" } : null}
                                                required
                                            >
                                                Phone Number
                                            </label>
                                            <span className="inputs-icon phone-icon"><i className="fas fa-phone-square"></i></span>
                                            <Error message={errors.phone_number} touched={touched.phone_number} />
                                        </motion.div>
                                    </section>
                                    <motion.div className="gender"
                                        animate={{ y: 0, scale: 1 }}
                                        initial={{ y: -200, scale: 1.2 }}
                                        exit={{ y: -200, scale: 1.3 }}
                                        transition={{ delay: 0.8 }}>
                                        <label htmlFor="male" className="male">
                                            <input
                                                type="radio"
                                                name="gender"
                                                id="male"
                                                value="Male"
                                                onChange={handleChange}
                                            />
                                            <i className="fas fa-male center-icons"></i>
                                            <span style={{ fontSize: "16px", textAlign: "center", display: "block", marginLeft: "5px" }}> Male</span>
                                        </label>
                                        <label htmlFor="female" className="female">
                                            <input
                                                type="radio"
                                                name="gender"
                                                id="female"
                                                value="Female"
                                                onChange={handleChange}
                                            />
                                            <i className="fas fa-female center-icons"></i>
                                            <span style={{ fontSize: "16px", textAlign: "center", display: "block", marginLeft: "5px" }}> Female</span>
                                        </label>
                                    </motion.div>
                                    <div className="sex-err">
                                        <Error message={errors.gender} touched={touched.gender} />
                                    </div>
                                    <motion.div className="planet"
                                        animate={{ y: 0, scale: 1 }}
                                        initial={{ y: -200, scale: 1.2 }}
                                        exit={{ y: -200 }}
                                        transition={{ delay: 1, scale: 1 }}>
                                        <select
                                            name="planet"
                                            id="planet-input"
                                            className="inputs-style"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                        >
                                            {this.state.planets.map(p => {
                                                return (
                                                    <option key={p.name} value={p.name}>{p.name}</option>
                                                )
                                            })}
                                        </select>
                                        <span className="arrow-select">
                                            <i className="fas fa-arrow-up"></i>
                                            <i className="fas fa-arrow-down"></i>
                                        </span>
                                    </motion.div>
                                    <motion.button
                                        animate={{ y: 0, opacity: 1 }}
                                        initial={{ y: -200, opacity: 0 }}
                                        exit={{ y: -200, opacity: 0 }}
                                        transition={{ delay: 1.1 }}
                                        type="submit"
                                        className="submit-button"
                                        disabled={isSubmitting}
                                    >
                                        <span className="submit-text">Submit</span>
                                    </motion.button>
                                </form>
                            )}
                    </Formik >
                </section>
            </motion.article >
        )
    }
}

const mapStateToProps = (state) => ({
    users: state.users,
    functions: state.functions
})

const mapDispatchToProps = (dispatch) => ({
    add: (name, secondName, email, id, password, repeatPassword, codePhone, phoneNumber, planet, gender) =>
        dispatch(actions.addUser({ name, secondName, id, email, password, repeatPassword, codePhone, phoneNumber, planet, gender }))

})

export default connect(mapStateToProps, mapDispatchToProps)(SingUp);