import React from 'react'
import { motion } from "framer-motion"
import { connect } from "react-redux"
import * as yup from "yup"
import { Formik } from "formik"
import Error from "./error"
import actions from "../redux/actions/add-user"
import Result from './result'

const headerVariants = {
    initial: {
        y: 40,
        opacity: 0
    },
    in: {
        y: 0,
        opacity: 1
    },
    out: {
        y: -40,
        opacity: 0
    }
}

const loginVariants = {
    initial: {
        x: -950,
        opacity: 0
    },
    in: {
        x: 0,
        opacity: 1,
    }
}

const buttonVariants = {
    initial: {
        y: 100,
        opacity: 0
    },
    in: {
        y: 0,
        opacity: 1
    },
}

const labelVariants = {
    initial: {
        opacity: 0,
        y: 20
    },
    in: {
        opacity: 1,
        y: 0
    },
    out: {
        opacity: 0,
        y: -20
    }
}

class SingIn extends React.Component {

    validationSchema = yup.object().shape({

        login_email: yup.string().email("Incorrect e-mail").required("You have to fill this input"),
        login_password: yup.string().min("4", "Password must have 4 or more characters").required("You have to fill this input"),
    })
    constructor(props) {
        super(props)
        this.state = {
            clicked: false
        }
    }
    render() {
        const labelUp = this.props.functions.functions

        return (
            <motion.section
                variants={loginVariants}
                initial="initial"
                animate="in"
                exit="initial"
                transition={{ delay: .3 }}
            >
                <motion.h1
                    className="headers"
                    variants={headerVariants}
                    initial="initial"
                    animate="in"
                    exit="out"
                    transition={{ duration: .7, delay: 1, type: "spring", stiffness: 200 }}
                >
                    Sing In
                    </motion.h1>
                <div className="sing-in-componets">
                    <Formik
                        initialValues={{
                            login_email: "",
                            login_password: ""
                        }}
                        validationSchema={this.validationSchema}
                        onSubmit={(values) => {
                            const validLogin = this.props.users.users.filter(u => u.email === values.login_email).filter(u => u.password === values.login_password)
                            if (validLogin.length !== 0) {
                                this.props.add(true)
                                const inputs = document.querySelectorAll("input");
                                inputs.forEach(i => i.value = "")
                                values.login_password = ""
                                values.login_email = ""
                            } else {
                                this.props.add(false)
                                const inputs = document.querySelector("#login-password-input");
                                inputs.value = ""
                                values.login_password = ""
                            }
                        }
                        }
                    >{({
                        handleBlur,
                        handleChange,
                        handleSubmit,
                        values,
                        errors,
                        touched,
                        isSubmitting
                    }) => (
                            <form action="POST" method="" onSubmit={handleSubmit}>
                                <article onChange={() => this.props.change(false)}>
                                    <div className="login-email-section" onClick={() => { this.setState({ clicked: false }) }}>
                                        <motion.input
                                            variants={loginVariants}
                                            initial="initial"
                                            animate="in"
                                            exit="initial"
                                            transition={{ duration: 0.3, transition: "ease" }}
                                            type="email"
                                            className="inputs-style"
                                            id="login-email-input"
                                            name="login_email"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            onClick={labelUp}
                                        />
                                        <motion.label
                                            variants={labelVariants}
                                            animate="in"
                                            exit="initial"
                                            initial="initial"
                                            transition={{ delay: .2, transition: "ease" }}
                                            htmlFor="login_email"
                                            className="labels-style"
                                            style={errors.login_email ? { color: "red" } : null}
                                        >
                                            E-mail
                                         </motion.label>
                                        <Error touched={touched.login_email} message={errors.login_email} />
                                    </div>
                                    <div className="login-password-section" onClick={() => { this.setState({ clicked: false }) }}>
                                        <motion.input
                                            variants={loginVariants}
                                            initial="initial"
                                            animate="in"
                                            transition={{ duration: 0.3, delay: .3 }}
                                            type="password"
                                            onClick={labelUp}
                                            className="inputs-style"
                                            exit="out"
                                            id="login-password-input"
                                            name="login_password"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                        />
                                        <motion.label
                                            variants={labelVariants}
                                            animate="in"
                                            exit="out"
                                            initial="initial"
                                            transition={{ delay: .5 }}
                                            htmlFor="login_password"
                                            className="labels-style"
                                            style={errors.login_password ? { color: "red" } : null}
                                        >
                                            Password
                                        </motion.label>
                                        <Error touched={touched.login_password} message={errors.login_password} />
                                    </div>
                                </article>
                                <motion.button
                                    className="submit-button"
                                    type="submit"
                                    variants={buttonVariants}
                                    initial="initial"
                                    animate="in"
                                    exit="initial"
                                    transition={{ delay: .6 }}
                                    onClick={() => this.setState({ clicked: true })}
                                >
                                    <span className="submit-text">Sing In</span>
                                </motion.button>
                                {this.state.clicked ?
                                    <Result
                                        loginPassword={values.login_password}
                                        loginEmail={values.login_email}
                                        isSubmitting={isSubmitting}
                                        userData={this.props.users.users}
                                    /> : null
                                }
                            </form>
                        )}
                    </Formik>
                </div>
            </motion.section >
        )
    }
}


const mapStateToProps = (state) => ({
    users: state.users,
    functions: state.functions,
    userLogin: state.userLogin
})

const mapDispatchToProps = dispatch => ({
    add: (setSubmitting) => dispatch(actions.userLogin(setSubmitting)),
    change: (userLogin) => dispatch(actions.userLogin(userLogin))
})

export default connect(mapStateToProps, mapDispatchToProps)(SingIn);